.meeting-notes-container {
  display: flex;
  height: calc(100vh - 200px);
  gap: 2rem;
}

.meeting-sidebar {
  width: 300px;
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  display: flex;
  flex-direction: column;
}

.sidebar-header {
  padding: 1.5rem;
  border-bottom: 1px solid #e0e0e0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.sidebar-header h3 {
  margin: 0;
  color: #2c3e50;
}

.new-meeting-btn {
  background-color: #1abc9c;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: background-color 0.3s;
}

.new-meeting-btn:hover {
  background-color: #16a085;
}

.meeting-list {
  flex: 1;
  overflow-y: auto;
  padding: 1rem;
}

.meeting-item {
  padding: 1rem;
  margin-bottom: 0.5rem;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.meeting-item:hover {
  background-color: #f8f9fa;
}

.meeting-item.active {
  background-color: #e8f4f8;
  border-left: 4px solid #1abc9c;
}

.meeting-item h4 {
  margin: 0 0 0.5rem 0;
  color: #2c3e50;
}

.meeting-item p {
  margin: 0;
  color: #7f8c8d;
  font-size: 0.9rem;
}

.meeting-content {
  flex: 1;
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  padding: 2rem;
  overflow-y: auto;
}

.meeting-form {
  max-width: 800px;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  color: #2c3e50;
  font-weight: 500;
}

.form-group input[type="text"],
.form-group input[type="date"],
.form-group textarea {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 1rem;
}

.form-group textarea {
  resize: vertical;
}

.input-with-button {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

.input-with-button input {
  flex: 1;
}

.input-with-button button {
  background-color: #3498db;
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.input-with-button button:hover {
  background-color: #2980b9;
}

.tag-list {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.tag {
  background-color: #e8f4f8;
  color: #3498db;
  padding: 0.25rem 0.75rem;
  border-radius: 16px;
  font-size: 0.9rem;
}

.agenda-list {
  margin-left: 1.5rem;
  color: #2c3e50;
}

.agenda-list li {
  margin-bottom: 0.5rem;
}

.action-item-form {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr auto;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.action-item-form input {
  padding: 0.5rem;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.action-item-form button {
  background-color: #3498db;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.action-item-form button:hover {
  background-color: #2980b9;
}

.action-items-list {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.action-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 0.75rem;
  background-color: #f8f9fa;
  border-radius: 4px;
}

.action-item.completed {
  opacity: 0.6;
  text-decoration: line-through;
}

.form-actions {
  display: flex;
  gap: 1rem;
  margin-top: 2rem;
}

.save-btn {
  background-color: #1abc9c;
  color: white;
  border: none;
  padding: 0.75rem 2rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s;
}

.save-btn:hover {
  background-color: #16a085;
}

.cancel-btn {
  background-color: #95a5a6;
  color: white;
  border: none;
  padding: 0.75rem 2rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s;
}

.cancel-btn:hover {
  background-color: #7f8c8d;
}

.meeting-detail {
  max-width: 800px;
}

.meeting-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid #e0e0e0;
}

.meeting-header h2 {
  margin: 0;
  color: #2c3e50;
}

.meeting-actions {
  display: flex;
  gap: 0.5rem;
}

.meeting-actions button {
  background-color: #3498db;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.meeting-actions button:hover {
  background-color: #2980b9;
}

.meeting-actions button:last-child {
  background-color: #e74c3c;
}

.meeting-actions button:last-child:hover {
  background-color: #c0392b;
}

.meeting-info {
  margin-bottom: 2rem;
}

.meeting-info p {
  margin-bottom: 0.5rem;
  color: #2c3e50;
}

.participants {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.agenda-section,
.notes-section,
.action-items-section {
  margin-bottom: 2rem;
}

.agenda-section h3,
.notes-section h3,
.action-items-section h3 {
  margin-bottom: 1rem;
  color: #2c3e50;
}

.empty-content {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  color: #95a5a6;
  font-size: 1.1rem;
}

.empty-state {
  text-align: center;
  padding: 2rem;
  color: #95a5a6;
}

.assignee {
  background-color: #e8f4f8;
  color: #3498db;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-size: 0.85rem;
}

.due-date {
  color: #e74c3c;
  font-size: 0.85rem;
}

@media (max-width: 768px) {
  .meeting-notes-container {
    flex-direction: column;
    height: auto;
  }
  
  .meeting-sidebar {
    width: auto;
    order: 2;
    max-height: 300px;
  }
  
  .meeting-content {
    order: 1;
    padding: 1rem;
    margin-bottom: 1rem;
  }
  
  .sidebar-header {
    padding: 1rem;
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }
  
  .sidebar-header h3 {
    text-align: center;
  }
  
  .new-meeting-btn {
    width: 100%;
    padding: 1rem;
  }
  
  .meeting-item {
    padding: 1rem;
  }
  
  .meeting-item h4 {
    font-size: 0.9rem;
  }
  
  .meeting-item p {
    font-size: 0.8rem;
  }
  
  .meeting-form {
    max-width: none;
  }
  
  .form-group input,
  .form-group textarea {
    padding: 1rem;
    font-size: 1rem;
  }
  
  .input-with-button {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .input-with-button button {
    width: 100%;
    padding: 1rem;
  }
  
  .action-item-form {
    grid-template-columns: 1fr;
    gap: 0.5rem;
  }
  
  .action-item-form button {
    padding: 1rem;
  }
  
  .form-actions {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .form-actions button {
    width: 100%;
    padding: 1rem;
  }
  
  .meeting-header {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }
  
  .meeting-actions {
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .meeting-actions button {
    flex: 1;
    min-width: 120px;
    padding: 1rem;
  }
  
  .meeting-info {
    margin-bottom: 1.5rem;
  }
  
  .participants {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.75rem;
  }
  
  .agenda-section ol,
  .notes-section,
  .action-items-section {
    margin-bottom: 1.5rem;
  }
  
  .action-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
    padding: 1rem;
  }
  
  .action-item input[type="checkbox"] {
    align-self: flex-start;
  }
}